import { Button, Image } from "react-bootstrap"
import thanks from '../assets/thanks.jpg'
import { useNavigate } from "react-router-dom"

const Thanks = ()=>{
    const navigate = useNavigate()
    return(
        <>
        <div >
        <Image src={thanks} fluid  style={{height:'100vh',width:'600vh'}}/>;
        <Button style={{position:'absolute', top:'70vh',left:'100vh'}} onClick={()=>navigate('/finalportfolio4/')}>Comeback</Button>
        </div>
        </>
    )
}
export default Thanks