
import { useState } from 'react'
import './land3.css'

const Land3 = ()=>{
    const [hapus,setHapus]= useState({
    
        display:'inline',
        hilangkan: ()=>{
            setHapus(data=>{
                return{
                    ...data,
                    display:'none'
                }
            })
        }
    })
    return(
        <>
        <div>

            <div style={{ marginTop:'30vh'}}>
                <h1 className='text-center' style={{fontFamily:'Bahnschrift Condensed',color:'aqua'}}>
                with a touch of animation on your website
                </h1>
            </div>
            
            
            <div style={{position:'relative',left:'170vh',width:'60vh',borderStyle:'solid',borderColor:'red',display:`${hapus.display}`}}>
            <div style={{display:`${hapus.display}`,color:'red'}}>
                github can't run animation
            </div>
            {/* icon hapus */}
            <div style={{display:`${hapus.display}`,position:'relative',left:'0.2vh',cursor:'pointer',color:'red'}}onClick={()=>hapus.hilangkan()}>
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-x-circle" viewBox="0 0 16 16">
            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14m0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16"/>
            <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708"/>
            </svg>
            </div>
            </div>
        <div className='wrapper'>


            <div className="dindingAtas"></div>
            <div className="dindingKanan"></div>
            <div className="dindingKiri"></div>
            <div className="dindingBawah"></div>
        

            <div className="objectAtas1"></div>
            <div className="objectAtas2"></div>
            <div className="objectTengah1"></div>
            <div className="objectTengah2"></div>
            <div className="objectBawah1"></div>
            <div className="objectBawah2"></div>

        
       </div>
       </div>
       </>
    )
}

export default Land3