import portfolio from '../assets/portfolio.jpg'
import restauant from '../assets/restaurant.jpg'
import volunteer from '../assets/volunteer.jpg'
import company from '../assets/Newcompany.jpg'
import { Card,Container,Col,Row, } from 'react-bootstrap'
import './page3.css'
import { useState } from 'react'

const Page3 = ()=>{

    const FileOriginal= (props)=>{
        return(
          
            <Card  >
                <Card.Img src={props.image} alt="Card image" />
                    <Card.ImgOverlay>
                        <Card.Title style={{color:'aqua',width:'5em',fontFamily:'Bahnschrift Condensed'}}>{props.title}</Card.Title>
                            
                             <Card.Text style={{color: "aqua" ,width:'8em',fontFamily:'Bahnschrift Condensed',borderStyle:"solid",borderColor:'black'}}>{props.sub}</Card.Text>
                    </Card.ImgOverlay>
            </Card>
        )
    }

    // -----------------------------------THEME DARK AND LIGHT LANDING 3

const [theme,setTheme]=useState({
  displayLight:"none",
  displayNight: "block",
  backgroundColor:'white',
  fontColor:'white',


  Light: ()=>{
    setTheme(data=>{
      return{
        ...data,
        displayLight:'none',
        displayNight:'block',
        backgroundColor:"white",
        fontColor:'aqua'
       
        
      }
    })
  },

  Night:()=>{
    setTheme(data=>{
      return{
        ...data,
        displayLight:'block',
        displayNight:'none',
        backgroundColor:'aqua',
        fontColor:'white'
        
        
        
      }
    })
  },
  
})

    return(
      <>
       {/* -------------------------------------------THEME--------------------------------------- */}

    <div style={{backgroundColor:`${theme.backgroundColor}`}} > 
    <div style={{marginLeft:'600px',paddingTop:'10px'}}>
    {/* LIGHT */}
    <div style={{position:'absolute',color:'yellow',cursor:'pointer',display:`${theme.displayLight}`}} onClick={()=>theme.Light()} >
      <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" fill="currentColor" class="bi bi-brightness-low-fill" viewBox="0 0 16 16">
          <path d="M12 8a4 4 0 1 1-8 0 4 4 0 0 1 8 0M8.5 2.5a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0m0 11a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0m5-5a.5.5 0 1 1 0-1 .5.5 0 0 1 0 1m-11 0a.5.5 0 1 1 0-1 .5.5 0 0 1 0 1m9.743-4.036a.5.5 0 1 1-.707-.707.5.5 0 0 1 .707.707m-7.779 7.779a.5.5 0 1 1-.707-.707.5.5 0 0 1 .707.707m7.072 0a.5.5 0 1 1 .707-.707.5.5 0 0 1-.707.707M3.757 4.464a.5.5 0 1 1 .707-.707.5.5 0 0 1-.707.707"/>
      </svg>
    </div>

    {/* DARK */}
    <div style={{position:'absolute',color:'aqua',marginLeft:'10px',display:`${theme.displayNight}`,cursor:'pointer'}} onClick={()=>theme.Night()} >
      <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-moon-stars-fill" viewBox="0 0 16 16">
        <path d="M6 .278a.768.768 0 0 1 .08.858 7.208 7.208 0 0 0-.878 3.46c0 4.021 3.278 7.277 7.318 7.277.527 0 1.04-.055 1.533-.16a.787.787 0 0 1 .81.316.733.733 0 0 1-.031.893A8.349 8.349 0 0 1 8.344 16C3.734 16 0 12.286 0 7.71 0 4.266 2.114 1.312 5.124.06A.752.752 0 0 1 6 .278"/>
        <path d="M10.794 3.148a.217.217 0 0 1 .412 0l.387 1.162c.173.518.579.924 1.097 1.097l1.162.387a.217.217 0 0 1 0 .412l-1.162.387a1.734 1.734 0 0 0-1.097 1.097l-.387 1.162a.217.217 0 0 1-.412 0l-.387-1.162A1.734 1.734 0 0 0 9.31 6.593l-1.162-.387a.217.217 0 0 1 0-.412l1.162-.387a1.734 1.734 0 0 0 1.097-1.097l.387-1.162zM13.863.099a.145.145 0 0 1 .274 0l.258.774c.115.346.386.617.732.732l.774.258a.145.145 0 0 1 0 .274l-.774.258a1.156 1.156 0 0 0-.732.732l-.258.774a.145.145 0 0 1-.274 0l-.258-.774a1.156 1.156 0 0 0-.732-.732l-.774-.258a.145.145 0 0 1 0-.274l.774-.258c.346-.115.617-.386.732-.732L13.863.1z"/>
      </svg>
    </div>
    </div>
    
        
      <Container>
        <div style={{position:'absolute',width:'195vh',marginTop:'5vh'}}>
          <h3 className='text-center' style={{fontFamily:'Bahnschrift Condensed',color:`${theme.fontColor}`}}>what is your website for</h3>
        </div>
    <Row>
      <Col lg={12}>
        <Row>
      <Col className='  pt-5 ' lg={4} >
        <div className= 'mt-5 pt-5'>
          <FileOriginal image={company} title='Company' sub="customer confidence"></FileOriginal>
            {/* <FileOriginal image={company} title='Company' sub='company identity' ></FileOriginal> */}
        </div>
    </Col>
    <Col className='pt-5 mt-5'>
    <h4 className='mt-5 pt-5 text-center' style={{fontFamily:'Bahnschrift Condensed',color: `${theme.fontColor}`}}>The kind of website we can make?</h4>
    </Col>
        </Row>
        <Row>
          <Col className='pt-5 offset-lg-2'lg={4}>
          <div className='  mt-5 pt-5'>
            <FileOriginal image={restauant} title='culinary' sub='Your restaurant'></FileOriginal>
          </div>
          </Col>
          <Col className='mt-5 pt-5'>
          <h4 className='mt-5 pt-5 text-center' style={{fontFamily:'Bahnschrift Condensed',color:`${theme.fontColor}`}}>
          Why a portfolio site matters
          </h4>
          </Col>
        </Row>
        <Row>
          <Col className='pt-5  offset-lg-4' lg={4}>
          <div className=' mt-5 pt-5'>
            <FileOriginal image={volunteer} title='volunteer' sub='your mind'>
            </FileOriginal>
          </div>
          </Col>
          <Col className='mt-5 pt-5 text-end'>
          <h4 className='mt-5 pt-5' style={{fontFamily:'Bahnschrift Condensed',color:`${theme.fontColor}`}}>what price trust</h4>
          </Col>
        </Row>
        <Row>
          <Col className='pt-5 ' lg={4}>
          <div className='exc'>
          <FileOriginal image={portfolio} title='in person' sub='more skilled'></FileOriginal>
        </div>
           </Col>
          <Col className='mt-5 pt-5'>
          <h1 className='mt-5 pt-5 text-center' style={{fontFamily:'Bahnschrift Condensed',color:`${theme.fontColor}`}}>2023: 5 billion web users/64.4</h1>
          </Col>
          </Row>
         </Col>
    </Row>
    
    
    </Container>
    </div>
    
    </>
   
    )
   
}

export default Page3